.Navigation {
    padding-top: 3%; 
    font-family: comfortaa, cursive;
    background-color: transparent;
    padding-right: 3%;
    padding-left: 3%;
    font-size: medium;
}


.Navigation .fontNav {
    color: #ffffff;
    font-size: large;

}

.Navigation .fontNav:hover {
    background-color: #ffffff;
    color: #00a6e5;
    border-radius: 2em;
}



@media screen and (max-width: 990px) {
    .Navigation {
        font-size: small;
    }
    .Navigation .fontNav {
        font-size: medium;
    }
}
